<template>
  <div>
    <v-toolbar flat color="white">
      <v-toolbar-title>Таблица сайтов:</v-toolbar-title>
      <v-divider class="mx-2" inset vertical></v-divider>
      <v-spacer></v-spacer>
      <v-btn class="mb-2" @click="pushData" v-if="status" color="error">Отправить</v-btn>
      <v-dialog v-model="dialog" max-width="500px">
        <v-btn slot="activator" color="primary" dark class="mb-2">Добавить</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm12 md12>
                  <v-text-field v-model="editedItem.url" label="URL страницы:"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Закрыть</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Сохранить</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table :headers="headers" :items="links" class="elevation-1">
      <template slot="items" slot-scope="props">
        <!-- <td>{{ props.item. }}</td> -->
        <td class="text-xs-left">
          <a :href="props.item.url" target="_blank">{{ props.item.url }}</a>
        </td>
        <td class="justify-center layout px-0">
          <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
          <v-icon small @click="deleteItem(props.item)">delete</v-icon>
        </td>
      </template>
      <template slot="no-data">
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </div>
</template>
<script>
export default {
  data: () => ({
    dialog: false,
    status: false,
    headers: [
      {
        text: "URL страницы",
        align: "left",
        sortable: false,
        value: "url"
      }
    ],
    links: [],
    editedIndex: -1,
    editedItem: {
      url: "",
      check_sum: 0
    },
    defaultItem: {
      url: "",
      check_sum: 0
    }
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Новый элемент" : "Добавить элемент";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.initialize();
  },

  methods: {
    pushData() {
      fetch("/rewrite_json.php", {
        method: "POST",
        headers: {
          Accept: "application/json, text/plain, */*",
          "Content-Type": "application/json"
        },
        body: JSON.stringify(this.links)
      })
        .then(r => r.json())
        .then(data => confirm("OK"));
       this.status = false;
    },
    initialize() {
      fetch("/rewrite_json.php")
        .then(r => r.json())
        .then(data => (this.links = data));
    },

    editItem(item) {
      this.editedIndex = this.links.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.links.indexOf(item);
      confirm("Вы точно хотите удалить?") && this.links.splice(index, 1);
      this.status = true;
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.links[this.editedIndex], this.editedItem);
        this.status = true;
      } else {
        this.links.push(this.editedItem);
        this.status = true;
      }
      this.close();
    }
  }
};
</script>